---
body {
  background-color: #1a1b26;
  color: #c0caf5;
  font-family: 'Fira Code', monospace;
  line-height: 1.6;
}

h1, h2, h3, h4, h5, h6 {
  color: #7aa2f7;
  font-weight: 700;
  letter-spacing: 0.02em;
  margin-top: 2rem;
  margin-bottom: 1rem;
}

h1 {
  font-size: 2.2rem;
  border-bottom: 2px solid #292e42;
  padding-bottom: 0.5rem;
}

h2 {
  font-size: 1.6rem;
  border-left: 4px solid #7aa2f7;
  padding-left: 0.7rem;
}

h3 {
  font-size: 1.2rem;
}

.wrapper, .page-content, .site-footer, .site-header {
  background: #222436;
  border-radius: 10px;
  box-shadow: 0 2px 16px 0 #16161e44;
  padding: 1.5rem 2rem;
  margin-bottom: 2rem;
}

main.page-content > .wrapper {
  background: #24283b;
}

hr {
  border: none;
  border-top: 1px solid #292e42;
  margin: 2rem 0;
}

a {
  color: #7dcfff;
  text-decoration: none;
  transition: color 0.2s;
}

a:hover {
  color: #bb9af7;
}

code, pre {
  background-color: #292e42;
  color: #c0caf5;
  border-radius: 4px;
}

pre {
  padding: 1rem;
  border-radius: 6px;
  overflow-x: auto;
}

.site-header, .site-footer {
  background: #1a1b26;
  border-radius: 0 0 10px 10px;
  box-shadow: 0 2px 8px 0 #16161e22;
}

.site-title, .footer-heading {
  color: #7aa2f7 !important;
  font-family: 'Fira Code', monospace;
  font-size: 1.5rem;
}

.site-nav .page-link {
  color: #7dcfff;
  margin-right: 1.2rem;
  font-weight: 500;
}

.site-nav .page-link:hover {
  color: #bb9af7;
}

ul, ol {
  margin-left: 2rem;
}

blockquote {
  background: #292e42;
  color: #c0caf5;
  border-left: 4px solid #7aa2f7;
  margin: 1.5rem 0;
  padding: 1rem 1.5rem;
  border-radius: 6px;
  font-style: italic;
}

.nav-links {
  display: flex;
  justify-content: space-between;
  margin-top: 2rem;
  font-size: 0.9rem;
}

.nav-links a {
  color: #7dcfff;
  text-decoration: none;
}

.nav-links a:hover {
  color: #bb9af7;
}
